import React, { useState, useEffect } from "react";
import Sidebar from "../../../components/Admin/Sidebar";
import TopBar from "../../../components/Admin//TopBar";
import DataTable from "react-data-table-component";
import { FaTrashAlt } from "react-icons/fa";
import { FaEdit } from "react-icons/fa";
import { useNavigate } from "react-router-dom";
import { FaPlus } from "react-icons/fa6";
import { Modal, Button } from "react-bootstrap";
import { Link } from "react-router-dom";
import SuccessAlert from "../../../components/Admin/SuccessAlert";
import Input from "../../../components/Admin/Input";
import axios from "axios";
function AdminModuleAdd() {
  const navigate = useNavigate();
  const [isOpen, setIsOpen] = useState(false);
  const [show, setShow] = useState(false);
  const [successMessage, setsuccessMessage] = useState("");
  const [deleteId, setdeleteId] = useState(false);
  const [showEdit, setshowEdit] = useState(false);
  const [editdata, seteditdata] = useState("");
  const toggleNavAdmin = () => {
    setIsOpen(!isOpen); // Toggle the state between true and false
  };
  const apiUrl = "https://blueprintcatalyst.com/api/admin/module/";
  const [records, setRecords] = useState([]);
  useEffect(() => {
    document.title = "Add Module - Admin";
  }, []);
  useEffect(() => {
    getmodule();
  }, []);
  const getmodule = async () => {
    let formData = {
      user_id: "",
    };
    try {
      const res = await axios.post(apiUrl + "getmodulelist", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      var respo = res.data.results;
      setRecords(respo);
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  const handleEdit = async (id) => {
    setshowEdit(true);
    let formData = {
      id: id,
    };
    try {
      const res = await axios.post(apiUrl + "getmodulerecord", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      var respo = res.data.results;
      console.log(respo);
      seteditdata(respo[0]);
      setMaxLimit(respo[0].max_limit);
      const fullPath = respo[0].video;
      const fileName = fullPath.split("\\").pop();
      setFile(fileName);
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  const columns = [
    {
      name: "Name",
      selector: (row) => row.name,
      sortable: true,
      className: "age",
    },
    {
      name: "Status",
      selector: (row) => row.status,
      sortable: true,
      className: "age",
    },
    {
      name: "Action",
      cell: (row) => (
        <div className="d-flex align-items-center gap-3">
          <button
            type="button"
            onClick={() => handleEdit(row.id)}
            className="dataedit_btn text-success border-0 fs-5"
            title="Edit"
          >
            <FaEdit />
          </button>
          <button
            type="button"
            onClick={() => handledelete(row.id)}
            className="dataedit_btn text-danger border-0 fs-5"
            title="Delete"
          >
            <FaTrashAlt />
          </button>
        </div>
      ),
      ignoreRowClick: true,
      allowOverflow: true,
      button: true,
    },
  ];
  const handledelete = (id) => {
    setdeleteId(id);
    setShow(true);
  };
  const handleClose = () => {
    setShow(false);
    setshowEdit(false);
  };
  const [searchQuery, setSearchQuery] = useState("");

  // Define filtered data based on the search query
  const filteredRecords = records.filter((record) =>
    Object.values(record)
      .join(" ")
      .toLowerCase()
      .includes(searchQuery.toLowerCase())
  );
  const handleConfirmDelete = async () => {
    let formData = {
      id: deleteId,
    };
    try {
      const res = await axios.post(apiUrl + "videodelete", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      var respo = res.data.results;
      getmodule();
      setShow(false);
      setTimeout(() => {
        setsuccessMessage("Video deleted successfully");
      }, 1000); // 3
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
    // Close the modal after deletion
  };
  const [maxLimit, setMaxLimit] = useState("");
  const [file, setFile] = useState(null);
  const [filenew, setFilenew] = useState(null);
  const [error, setError] = useState("");
  const handleFileChange = (e) => {
    const selectedFile = e.target.files[0];

    if (selectedFile) {
      const maxSize = 15 * 1024 * 1024; // 15 MB in bytes

      if (selectedFile.size > maxSize) {
        setError("File size should not exceed 15MB.");
        e.target.value = null; // clear the selected file
        setFile(null);
        setFilenew(null);
      } else {
        setError("");
        setFilenew(selectedFile);
        setFile(selectedFile);
      }
    }
  };
  const handleSubmit = async (e) => {
    e.preventDefault();

    let formdata = {
      name: e.target.elements.name.value,
      status: e.target.elements.status.value,
      description: e.target.elements.description.value,
      price: e.target.elements.price.value,
      annual_price: e.target.elements.annual_price.value,
    };

    try {
      const res = await axios.post(apiUrl + "savemodule", formdata, {
        headers: {
          Accept: "application/json",
        },
      });

      var respo = res.data.results;
      setsuccessMessage("Successfully Created");

      setTimeout(() => {
        navigate("/admin/module/list");
      }, 1000);
    } catch (err) {
      if (err.response) {
        console.error("Server error:", err.response.data);
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };

  return (
    <>
      <div>
        <div className="d-flex align-items-start gap-0">
          <Sidebar />
          <div className="d-flex flex-column gap-0 w-100">
            <TopBar />
            <section className="dashboard_adminh">
              <div className="container-xl">
                <div className="row gy-4">
                  <div className="col-8">
                    {successMessage && (
                      <SuccessAlert
                        message={successMessage}
                        onClose={() => setsuccessMessage("")}
                      />
                    )}
                    <div className="card p-4">
                      <h5 className="mb-4">Add Module</h5>
                      <form
                        action="javascript:void(0)"
                        onSubmit={handleSubmit}
                        method="post"
                        className="d-flex flex-column gap-4 align-items-start"
                      >
                        <div className="w-100">
                          <label
                            htmlFor="exampleInputLimit"
                            className="form-label text-dark"
                          >
                            Name <span className="text-danger">*</span>
                          </label>
                          <input
                            type="text"
                            name="name"
                            required
                            min="1"
                            placeholder="Enter name"
                            className="form-control"
                            id="exampleInputLimit"
                          />
                        </div>
                        <div className="mb-3 text-start w-100">
                          <label
                            htmlFor="exampleInputLimit"
                            className="form-label text-dark"
                          >
                            Status <span className="text-danger">*</span>
                          </label>
                          <select
                            className="form-control w-100"
                            name="status"
                            required
                          >
                            <option value="">--Select--</option>
                            <option value="Active">Active</option>
                            <option value="Inactive">In Active</option>
                          </select>
                        </div>
                        <div className="mb-3 text-start w-100">
                          <label
                            htmlFor="exampleInputLimit"
                            className="form-label text-dark"
                          >
                            Description <span className="text-danger">*</span>
                          </label>
                          <textarea
                            className="form-control"
                            name="description"
                            placeholder="Enter description"
                          ></textarea>
                        </div>
                        <div className="mb-3 text-start w-100">
                          <label
                            htmlFor="exampleInputLimit"
                            className="form-label text-dark"
                          >
                            One-Time Fee
                          </label>
                          <input
                            onInput={(e) => {
                              let value = e.target.value.replace(/\D/g, ""); // Remove non-digits
                              value = value.substring(0, 16); // Max 16 digits
                              value = value.replace(/(.{4})/g, "$1 ").trim(); // Add space every 4 digits
                              e.target.value = value;
                            }}
                            type="number"
                            className="form-control"
                            name="price"
                            placeholder="Enter price"
                          />
                        </div>
                        <div className="mb-3 text-start w-100">
                          <label
                            htmlFor="exampleInputLimit"
                            className="form-label text-dark"
                          >
                            Annual Fee
                          </label>
                          <input
                            onInput={(e) => {
                              let value = e.target.value.replace(/\D/g, ""); // Remove non-digits
                              value = value.substring(0, 16); // Max 16 digits
                              value = value.replace(/(.{4})/g, "$1 ").trim(); // Add space every 4 digits
                              e.target.value = value;
                            }}
                            type="number"
                            className="form-control"
                            name="annual_price"
                            placeholder="Enter price"
                          />
                        </div>

                        <div className="d-grid">
                          <button type="submit" className="btn btn-primary">
                            Submit
                          </button>
                        </div>
                      </form>
                    </div>
                  </div>
                </div>
              </div>
            </section>
          </div>
        </div>
      </div>
      <Modal className="text-white" show={show} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>Confirm Deletion</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <p>Are you sure you want to delete this record?</p>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Cancel
          </Button>
          <Button variant="danger" onClick={handleConfirmDelete}>
            Confirm Delete
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
}

export default AdminModuleAdd;
